.about{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 70%;
    margin: auto;
    min-height: 100vh;
    justify-content: center;
    vertical-align: middle;
    gap: 20px;
    margin-top: 30px;
}
.aboutImage{
    width: 50px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    background-color: orange;
    padding: 10px;
    border-radius: 100%;
}
.aboutHeading{
    color: #555067;
    font-size: 50px;
    font-weight: 600;
}
.aboutContent{
    color: white;
    font-size: 20px;
}
@media (max-width: 1000px){
    .about{
        width: 90%;
    }
    .aboutContent{
        font-size: 15px;
    }
}