.socials{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 70%;
    margin: auto;
    gap: 20px;
    min-height: 100vh;
    justify-content: center;
    vertical-align: middle;
}
.socialsHeading{
    color: #555067;
    font-size: 50px;
    font-weight: 600;
}
.social{
    display: flex;
    gap: 30px;
    justify-content: center;
    flex-wrap: wrap;
}