.container {
  display: flex;
  align-items: center;
  padding: 20px;
  margin: auto;
  justify-content: center;
  gap: 30px;
  min-height: 85vh;
}
.typer{
    color: orange;
}
.cv{
    background-color: transparent;
    color: white;
    padding: 20px;
    border:1px solid white;
    border-radius: 30px;
    margin-top: 20px;
}
.cv:hover{
    background-color: inherit;
}

.left-content {
  text-align: center;
  margin-bottom: 20px;
    min-width: 50%;
}

.line1 {
  color: #555067;
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 10px;
}

.line2 {
  color: white;
  font-size: 100px;
  font-weight: 600;
  margin-bottom: 10px;
}

.line3 {
  color: white;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 10px;
}
.right-content {
  text-align: center;
  min-width: 50%;
}

.myimage {
  max-width: 100%;
  height: auto;
}

@media (max-width: 1000px) {
  .container {
    flex-direction: column;
  }
  .line2{
    font-size: 50px;
  }

  .left-content {
    text-align: center;
    margin-bottom: 20px;
  }

  .right-content {
    text-align: center;
  }
  .image{
    max-width: 80%;
  }
}
@media (max-width: 500px) {
    .line1{
        font-size: 20px;
    }
    .line2{
        font-size: 40px;
    }
    .line3{
        font-size: 20px;
    }
}
