.skills{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 70%;
    margin: auto;
    gap: 20px;
    min-height: 100vh;
    justify-content: center;
    vertical-align: middle;
}
.skillsHeading{
    color: #555067;
    font-size: 50px;
    font-weight: 600;
}
.allSkills{
    display: flex;
    flex-direction: column;
    gap: 70px;
}

.eachType{
    display: flex;
    flex-direction: column;
    gap: 30px;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
}
.skillSide{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    color: orange;
    background-color: #25185E;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    margin: auto;
    align-items: center;
}
.indiSkill{
    gap: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin: auto;
    justify-content: center;
}
.skillsImage{
    width: 50px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    background-color: orange;
    padding: 10px;
    border-radius: 100%;
}

@media (max-width: 1000px){
    .skills{
        width: 90%;
    }
   
}