.experience{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 70%;
    margin: auto;
    gap: 20px;
    min-height: 100vh;
    justify-content: center;
    vertical-align: middle;
    
}
.allExp{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
}
.experienceHeading{
    color: #555067;
    font-size: 50px;
    font-weight: 600;
}
.experienceImage{
    width: 50px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    background-color: orange;
    padding: 10px;
    border-radius: 100%;
}