.eachProject{
    background-color: white;
    border: 1px solid white;
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    border-radius: 20px;
    border: 1px solid white;
}
.heading{
    font-size: 25px;
    font-weight: 600;
    color: black;
}
.buttons{
    display: flex;
    gap: 20px;
}
.plink{
    text-decoration: none;
    color: black;
}
.plink:hover{
    background-color: transparent;
}

.btn{
    color: black;
    padding: 10px;
    width: fit-content;
    border: 1px solid black;
}
.btn:hover{
    background-color: #555067;
    border: 1px solid black;
}
.image{
    width: 250px;
    border-radius: 10px;
}