@tailwind base;
@tailwind components;
@tailwind utilities;

::-webkit-scrollbar {
      width: 12px;
    }

    ::-webkit-scrollbar-thumb {
      background-color: orange; /* Change this to your desired thumb color */
      border-radius: 6px;
    }

    ::-webkit-scrollbar-track {
      background-color: #f1f1f1; /* Change this to your desired track color */
      border-radius: 6px;
    }